.consultation-container {
    margin: 1.5rem;
}

.consultation-container .columns {
    display: flex;
}

.consultation-container .column.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.consultation-container .actions {
    margin: 2rem auto;    
    width: max-content;
}

.consultation-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.details-list li {
    margin: 1rem .5rem;
}

.consultation-container .list-fragment li {
    cursor: pointer;
}

@media only screen and (min-width: 768px) {
    .details-list li {
        margin: 1.5rem .5rem;    }
}