.Main {
    background: #fff url('../../../assets/main_background_light.jpg') no-repeat center;
    background-size: cover;
    height: 90vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 12vh;
}

.main-label {
    margin: 1em;
    color: white;
    text-align: center;
}

@media only screen and (min-width: 768px) {
    .main-label {
        font-size: 1.5em;
        margin: 1em;
        color: white;
        text-align: center;
    }
}