body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 1.5;
  text-align: left;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p, li {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.548);
}

.label-big {
  font-size: 1.5em;
}

.label,
.label-big {
  font-weight: bold;
}

h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: bold;
  font-style: italic;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
}

.h3, h3 {
  font-size: 1.75rem;
}

.h4, h4 {
  font-size: 1.5rem;
}

.h5, h5 {
  font-size: 1.25rem;
}

.h6, h6 {
  font-size: 1rem;
}

.primary {
  color: #cf5524;
}

.alt-primary {
  color: #6cc6c6;
}

.secondary {
  color: #facb0b;
}

.alt-secondary {
  color: #d8e994;
}

.title p {
  font-style: italic;
  margin: 0px;
}

*,
::after,
::before {
  box-sizing:border-box;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

@media only screen and (min-width: 768px) {

  .title p {
    font-size: 1.2rem;
  }

}

@media only screen and (max-width: 768px) {

  * {
    -webkit-tap-highlight-color: transparent;
  }

}