nav {
    height: 45%;
    width: 100%;
    margin: 0;
    top: 0;
    color: rgba(0, 0, 0, 0.548);
    background-color: white;
    font-size: 16px;
}

.logo-big-container {
    height: 85%;
    background-color: #f7f7f7
}

.logo-big {
   width: 100%;
   background: #f7f7f7 url('../../assets/logo.png') no-repeat center;
   background-size: 60%;
   text-align: center;
}

.fixed-nav ul {
    height: 15%;
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.fixed-nav ul.colored {
    background-color: #f7f7f7
}

.fixed-nav li {
    height: 100%;
    flex: 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in;
}

.fixed-nav li.hidden {
    display: none;
}

.fixed-nav li:not(.main-button) {
    cursor: pointer;
}

.fixed-nav li.main-button {
    padding-left: 10px;
    flex-grow: 1;
    justify-content: flex-start;
}

.fixed-nav li:not(.main-button):hover {
    border-bottom: 3px solid #facb0b;
    transition: all 0.1s ease-in;
}

.fixed-nav {
    display: block;
    position: fixed;
    z-index: 1;
}

.fixed-nav .navbar-logo {
    height: 100%;
    cursor: pointer;
}

.logo-big {
    width: 100%;
    height: 85%;
    background: #f7f7f7 url('../../assets/logo.png') no-repeat center;
    background-size: 60%;
    text-align: center;
 }

 @media only screen and (min-width: 768px) {   
    .logo-big {
        background-size: 20%;
    }
}