.Footer {
    width: 100%;
    display: flex;
    background-color: #cf5524;
    padding: .5em;
    justify-content: space-between;
    align-items: baseline;
}

.Footer .icons {
    display: flex;
}

.Footer .icons .round-icon {
    padding: .2em;
    margin: .2em;
    background-color: #facb0b;;
    border-radius: 100%;
    color: white;
}