.Contact {
    width: 90%;
    margin: 1em 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
}

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    display: none;
}

.profile-info {
    margin: auto;
}

.social {
    display: flex;
}

@media only screen and (min-width: 768px) {
    .Contact {
        flex-direction: row;
    }

    .Contact .profile {
        display: flex;
        flex-direction: column;
        height: min-content;
        padding-top: 2em;
    }

    .profile-img {
        display: block;
        width: 35%;
        border-radius: 5%;
        margin: .5em auto;
    }

    .Contact > div {
        width: 100%;
    }
}