.service-icon-wrapper {
    display: flex;
    flex-direction: column;
}

.service-image {
    height: 12vh;
    width: auto;
    margin: auto;
    transition: border 0.1s ease-in;
}

.service-title {
    margin: auto;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    font-size: 1.1rem;
}