.Consultations {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
}

.Consultations div {
    width: 100%;
}

.Consultations p,
.Consultations ul {
    margin: 1rem auto;
    list-style: none;
    width: fit-content;
}

.Consultations ul li:before {
    content: '✓';
    font-size: 1.5rem;
}

.Consultations p {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    width: max-content;
    font-weight: bold;
    text-transform: uppercase;
}

.Consultations .column ul li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}

@media only screen and (min-width: 768px) {
    .Consultations {
        flex-direction: row;
    }
}