.Info {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    margin-top: 5vh;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

.services {
    width: 100%;
}

.services > div {
    flex: 1 1 0px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 1em;
}

.service-description {
    height: auto;
    width: 100%;
    display: flex;
}

.card-col {
    width: max-content;
    margin: 1rem;
    display: inline-block;
}

.card-col-info {
    width: 25rem;
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.548);
    margin-left: 1rem;
}

.form-col {
    display: inline-block;
    flex-grow: 2;
    margin: 1rem;
}

.services-expanded {
    display: none;
}

@media only screen and (min-width: 768px) {
    .services {
        display: flex;
    }

    .services div:not(.selected):hover .service-image {
        border-bottom: 3px solid #facb0b;
    }
    
    .services .selected .service-image {
        border-bottom: 3px solid #facb0b;
    }
    
    .services .selected .service-title {
        color: #cf5524;
        font-weight: bold;
        font-style: italic;
        font-size: 1.1rem;
    }

    .services-expanded {
        display: block;
        min-height: 30rem;
        width: 100%;
    }

    .services .service {
        display: none;
    }

    .service .title {
        margin-bottom: 1rem;
    }

    #listContentExpanded,
    #listContentExpanded p,
    #listContentExpanded li {
        font-size: 1rem;
    }
}