.About {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7;
}

.frame {
    margin-top: 2rem;
}

.About .profile {
    border-radius: 5%;
    width: 50%;
    margin: auto;
}

blockquote{
  font-size: 1em;
  margin: 1.5em auto;
  font-family:Open Sans;
  font-style:italic;
  color: #555555;
  padding:1.2em 30px 1.2em 75px;
  border-left:8px solid #facb0b ;
  line-height:1.6;
  position: relative;
  background:#EDEDED;
}

blockquote::before{
  font-family:Arial;
  content: "\201C";
  color:#facb0b;
  font-size:4em;
  position: absolute;
  left: 10px;
  top:-10px;
}

blockquote::after{
  content: '';
}

blockquote span{
  display:block;
  color:#333333;
  font-style: normal;
  font-weight: bold;
  margin-top:1em;
}

.About p,
.About li {
  color: rgba(0, 0, 0, 0.548);
  font-size: 14px;
}

@media only screen and (min-width: 768px) {
  .About {
    flex-direction: row;
    padding: 1rem 1rem 3rem 1rem;
  }

  .About .profile {
    height: 100%;
    border-radius: 5%;
    width: 20%;
    margin: 2em;
  }
}