body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 1.5;
  text-align: left;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p, li {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.548);
}

.label-big {
  font-size: 1.5em;
}

.label,
.label-big {
  font-weight: bold;
}

h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: bold;
  font-style: italic;
  margin-bottom: .5rem;
  font-weight: 500;
  line-height: 1.2;
}

.h3, h3 {
  font-size: 1.75rem;
}

.h4, h4 {
  font-size: 1.5rem;
}

.h5, h5 {
  font-size: 1.25rem;
}

.h6, h6 {
  font-size: 1rem;
}

.primary {
  color: #cf5524;
}

.alt-primary {
  color: #6cc6c6;
}

.secondary {
  color: #facb0b;
}

.alt-secondary {
  color: #d8e994;
}

.title p {
  font-style: italic;
  margin: 0px;
}

*,
::after,
::before {
  box-sizing:border-box;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

@media only screen and (min-width: 768px) {

  .title p {
    font-size: 1.2rem;
  }

}

@media only screen and (max-width: 768px) {

  * {
    -webkit-tap-highlight-color: transparent;
  }

}
.Main {
  background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
  background-size: cover;
  height: 90vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.Main {
    background: #fff url(/web-test/static/media/main_background_light.0a052866.jpg) no-repeat center;
    background-size: cover;
    height: 90vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 12vh;
}

.main-label {
    margin: 1em;
    color: white;
    text-align: center;
}

@media only screen and (min-width: 768px) {
    .main-label {
        font-size: 1.5em;
        margin: 1em;
        color: white;
        text-align: center;
    }
}
.About {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7;
}

.frame {
    margin-top: 2rem;
}

.About .profile {
    border-radius: 5%;
    width: 50%;
    margin: auto;
}

blockquote{
  font-size: 1em;
  margin: 1.5em auto;
  font-family:Open Sans;
  font-style:italic;
  color: #555555;
  padding:1.2em 30px 1.2em 75px;
  border-left:8px solid #facb0b ;
  line-height:1.6;
  position: relative;
  background:#EDEDED;
}

blockquote::before{
  font-family:Arial;
  content: "\201C";
  color:#facb0b;
  font-size:4em;
  position: absolute;
  left: 10px;
  top:-10px;
}

blockquote::after{
  content: '';
}

blockquote span{
  display:block;
  color:#333333;
  font-style: normal;
  font-weight: bold;
  margin-top:1em;
}

.About p,
.About li {
  color: rgba(0, 0, 0, 0.548);
  font-size: 14px;
}

@media only screen and (min-width: 768px) {
  .About {
    flex-direction: row;
    padding: 1rem 1rem 3rem 1rem;
  }

  .About .profile {
    height: 100%;
    border-radius: 5%;
    width: 20%;
    margin: 2em;
  }
}
.service-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form-element {
    display: block;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    margin: 2rem;
}

.form-title {
    margin-bottom: .7rem;
}
.Contact {
    width: 90%;
    margin: 1em 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
}

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    display: none;
}

.profile-info {
    margin: auto;
}

.social {
    display: flex;
}

@media only screen and (min-width: 768px) {
    .Contact {
        flex-direction: row;
    }

    .Contact .profile {
        display: flex;
        flex-direction: column;
        height: -webkit-min-content;
        height: -moz-min-content;
        height: min-content;
        padding-top: 2em;
    }

    .profile-img {
        display: block;
        width: 35%;
        border-radius: 5%;
        margin: .5em auto;
    }

    .Contact > div {
        width: 100%;
    }
}
#listContentWrapper {
    display: flex;
    width: 100%;
}

#listContentContainer {
    width: 100%;
}

#listContentExpanded {
    display: none;
    margin: auto;
}

.list-fragment {
    list-style-type: none;
}

.list-fragment li {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    margin: 1.2rem;
    text-align: center;
    font-size: 1.3rem;
}

@media only screen and (min-width: 768px) {
    #listContentContainer {
        width: 50%;
    }
    #listContentExpanded {
        display: block;
        width: 50%;
    }
    #contentFragment {
        display: none;
    }
    .list-fragment li.extra-margin {
        margin: 3rem;
    }
}
.Consultations {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
}

.Consultations div {
    width: 100%;
}

.Consultations p,
.Consultations ul {
    margin: 1rem auto;
    list-style: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.Consultations ul li:before {
    content: '\2713';
    font-size: 1.5rem;
}

.Consultations p {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    font-weight: bold;
    text-transform: uppercase;
}

.Consultations .column ul li {
    color: rgba(0, 0, 0, 0.548);
    font-size: 1.2rem;
}

@media only screen and (min-width: 768px) {
    .Consultations {
        flex-direction: row;
    }
}
.consultation-container {
    margin: 1.5rem;
}

.consultation-container .columns {
    display: flex;
}

.consultation-container .column.main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.consultation-container .actions {
    margin: 2rem auto;    
    width: -webkit-max-content;    
    width: -moz-max-content;    
    width: max-content;
}

.consultation-container .form {
    padding-left: 3rem;
    border-left: 1px #facb0b solid;
}

.details-list li {
    margin: 1rem .5rem;
}

.consultation-container .list-fragment li {
    cursor: pointer;
}

@media only screen and (min-width: 768px) {
    .details-list li {
        margin: 1.5rem .5rem;    }
}
.workshop-container {
    margin: 1.5rem;
}

.workshop-container .title p {
    vertical-align: center;
}
.colaborations-container {
    margin: 1.5rem;
}

.colaborations-container img {
    width: 80%;
}

@media only screen and (min-width: 768px) {
    .colaborations-container img {
        width: 60%;
    }
}
@media only screen and (min-width: 1550px) {
    .colaborations-container img {
        width: 40%;
    }
}
.service-icon-wrapper {
    display: flex;
    flex-direction: column;
}

.service-image {
    height: 12vh;
    width: auto;
    margin: auto;
    transition: border 0.1s ease-in;
}

.service-title {
    margin: auto;
    color: rgba(0, 0, 0, 0.548);
    font-style: italic;
    font-size: 1.1rem;
}
.Info {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    margin-top: 5vh;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

.services {
    width: 100%;
}

.services > div {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 1em;
}

.service-description {
    height: auto;
    width: 100%;
    display: flex;
}

.card-col {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin: 1rem;
    display: inline-block;
}

.card-col-info {
    width: 25rem;
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.548);
    margin-left: 1rem;
}

.form-col {
    display: inline-block;
    flex-grow: 2;
    margin: 1rem;
}

.services-expanded {
    display: none;
}

@media only screen and (min-width: 768px) {
    .services {
        display: flex;
    }

    .services div:not(.selected):hover .service-image {
        border-bottom: 3px solid #facb0b;
    }
    
    .services .selected .service-image {
        border-bottom: 3px solid #facb0b;
    }
    
    .services .selected .service-title {
        color: #cf5524;
        font-weight: bold;
        font-style: italic;
        font-size: 1.1rem;
    }

    .services-expanded {
        display: block;
        min-height: 30rem;
        width: 100%;
    }

    .services .service {
        display: none;
    }

    .service .title {
        margin-bottom: 1rem;
    }

    #listContentExpanded,
    #listContentExpanded p,
    #listContentExpanded li {
        font-size: 1rem;
    }
}
.Footer {
    width: 100%;
    display: flex;
    background-color: #cf5524;
    padding: .5em;
    justify-content: space-between;
    align-items: baseline;
}

.Footer .icons {
    display: flex;
}

.Footer .icons .round-icon {
    padding: .2em;
    margin: .2em;
    background-color: #facb0b;;
    border-radius: 100%;
    color: white;
}
.Panel {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
nav {
    height: 45%;
    width: 100%;
    margin: 0;
    top: 0;
    color: rgba(0, 0, 0, 0.548);
    background-color: white;
    font-size: 16px;
}

.logo-big-container {
    height: 85%;
    background-color: #f7f7f7
}

.logo-big {
   width: 100%;
   background: #f7f7f7 url(/web-test/static/media/logo.df600be6.png) no-repeat center;
   background-size: 60%;
   text-align: center;
}

.fixed-nav ul {
    height: 15%;
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.fixed-nav ul.colored {
    background-color: #f7f7f7
}

.fixed-nav li {
    height: 100%;
    flex: 1 1;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease-in;
}

.fixed-nav li.hidden {
    display: none;
}

.fixed-nav li:not(.main-button) {
    cursor: pointer;
}

.fixed-nav li.main-button {
    padding-left: 10px;
    flex-grow: 1;
    justify-content: flex-start;
}

.fixed-nav li:not(.main-button):hover {
    border-bottom: 3px solid #facb0b;
    transition: all 0.1s ease-in;
}

.fixed-nav {
    display: block;
    position: fixed;
    z-index: 1;
}

.fixed-nav .navbar-logo {
    height: 100%;
    cursor: pointer;
}

.logo-big {
    width: 100%;
    height: 85%;
    background: #f7f7f7 url(/web-test/static/media/logo.df600be6.png) no-repeat center;
    background-size: 60%;
    text-align: center;
 }

 @media only screen and (min-width: 768px) {   
    .logo-big {
        background-size: 20%;
    }
}

.fixed-nav-alt .navbar-logo img {
    height: 100%;
}

.fixed-nav-alt .navbar-logo {
    text-align: left;
    text-align: initial;
}

.fixed-nav-alt a:not(.navbar-logo) {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.fixed-nav-alt a {
    height: 100%;
    width: 100%;
    color: inherit;
}

.fixed-nav-alt li {
    text-align: center;
}
