#listContentWrapper {
    display: flex;
    width: 100%;
}

#listContentContainer {
    width: 100%;
}

#listContentExpanded {
    display: none;
    margin: auto;
}

.list-fragment {
    list-style-type: none;
}

.list-fragment li {
    background-color: #f7f7f7;
    padding: .2rem .5rem;
    margin: 1.2rem;
    text-align: center;
    font-size: 1.3rem;
}

@media only screen and (min-width: 768px) {
    #listContentContainer {
        width: 50%;
    }
    #listContentExpanded {
        display: block;
        width: 50%;
    }
    #contentFragment {
        display: none;
    }
    .list-fragment li.extra-margin {
        margin: 3rem;
    }
}